export default "<template>\r\n\t<section>\r\n\t\t<p class=\"content\"><b>Selected:</b> {{ description }}</p>\r\n\t\t<b-field label=\"Find a JS framework\">\r\n\t\t\t<b-autocomplete\r\n\t\t\t\tis-rounded\r\n\t\t\t\t:items=\"frameworks\"\r\n\t\t\t\tplaceholder=\"e.g. Vue\"\r\n\t\t\t\t:icon=\"SearchIcon\"\r\n\t\t\t\tclear-on-select\r\n\t\t\t\tv-model:selected-items=\"selectedFrameworks\"\r\n\t\t\t>\r\n\t\t\t\t<template #empty=\"{ searchValue }\">No results found for {{ searchValue }}</template>\r\n\t\t\t</b-autocomplete>\r\n\t\t</b-field>\r\n\t</section>\r\n</template>\r\n<script lang=\"ts\">\r\nimport { BAutocomplete } from 'buetify/lib/components';\r\nimport BField from 'buetify/lib/components/form/field';\r\nimport { defineComponent, shallowRef, computed } from 'vue';\r\nimport SearchIcon from '../../../../../../components/icons/SearchIcon';\r\n\r\nconst frameworks = [\r\n\t'Angular',\r\n\t'Angular 2',\r\n\t'Aurelia',\r\n\t'Backbone',\r\n\t'Ember',\r\n\t'jQuery',\r\n\t'Meteor',\r\n\t'Node.js',\r\n\t'Polymer',\r\n\t'React',\r\n\t'RxJS',\r\n\t'Vue.js'\r\n];\r\n\r\nexport default defineComponent({\r\n\tname: 'autocomplete-example',\r\n\tcomponents: {\r\n\t\tBAutocomplete,\r\n\t\tBField\r\n\t},\r\n\tsetup() {\r\n\t\tconst selectedFrameworks = shallowRef([]);\r\n\t\tconst description = computed(() => selectedFrameworks.value.join(', '));\r\n\r\n\t\treturn {\r\n\t\t\tSearchIcon,\r\n\t\t\tframeworks,\r\n\t\t\tselectedFrameworks,\r\n\t\t\tdescription\r\n\t\t};\r\n\t}\r\n});\r\n</script>\r\n";